// 
export default [{
  path: '/',
  redirect: 'step1'
},
  // 
{
  path: 'step1',
  name: 'step1',
  meta: {
    // index
    index: 0,
    requiresAuth: true,
    keepAlive: false
  },
  // 
  component: resolve => require(['@/modules/caseHandle/step1/step1'], resolve)
},
  // 
{
  path: 'step2',
  name: 'step2',
  meta: {
    index: 1,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step2/step2'], resolve)
},
  // 
{
  path: 'step3',
  name: 'step3',
  props: true,
  meta: {
    index: 2,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step3/step3'], resolve)
},
  // 
{
  path: 'step4',
  name: 'step4',
  meta: {
    index: 3,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step4/step4'], resolve)
},

  // 
{
  path: 'step5',
  name: 'step5',
  meta: {
    index: 4,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step5/step5'], resolve)
},

  // 
{
  path: 'step6',
  name: 'step6',
  meta: {
    index: 5,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step6/step6'], resolve)
}
]



